ámicas que todos conocen, pues las jugaban cuando eran pequeños o con sus hijos. Juegos como la “lleva”, el “escondite”, “saltar la cuerda”, “carreras”, entre otros, hacen parte de este grupo. ")])])])]),_c('row',{attrs:{"numero":"3","titulo":"Juegos de competencias"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img13.png"),"alt":""}})])]),_c('div',{staticClass:"col-md-7 mb-4 mb-md-0"},[_c('p',[_vm._v("El objetivo de los juegos de competencia es promover la comunicación y el compromiso entre los integrantes del grupo, ya que son necesarios para alcanzar la sinergia necesaria para llegar a la meta.")])])])]),_c('row',{attrs:{"numero":"4","titulo":"Juegos de roles "}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img14.png"),"alt":""}})])]),_c('div',{staticClass:"col-md-7 mb-4 mb-md-0"},[_c('p',[_vm._v("Los juegos de roles reflejan situaciones, tomando como referente a alguien conocido dentro de la comunidad o una figura pública. El objetivo del juego es promover la empatía con otros actores dentro de la sociedad, permitiendo que aquel que desempeña el rol se ubique en los zapatos de esta persona o situación y reconozca sus beneficios y dificultades.")])])])])],1)],1),_vm._m(5),_c('h3',{staticClass:"mt-5 mb-5"},[_vm._v("Cartografía social")]),_c('p',[_vm._v("La cartografía social es una herramienta de planificación territorial desde un enfoque colectivo y participativo en el que los propios actores o participantes recrean situaciones o proyectan formas de ordenación desde un mapa, que se construye entre todos, donde los intereses culturales, la tenencia histórica de la tierra o las actividades económicas derivadas de los recursos hacen del ejercicio una construcción colectiva de un territorio a partir de la mirada de donde se aborda el tema